import { combineReducers } from 'redux';

const searchReducer = (state = {}, action) => {
	switch(action.type){
		case 'SEARCH':
			return {...state, Recipe:action.payload};
		case 'SUBMIT_SEARCH':
			return {...state, Recipes:action.payload.hits};
		default:
			return state;
	}
}
export default combineReducers({
	search: searchReducer
})