import React from 'react';
import { connect } from 'react-redux';
import {searchRecipe, submitSearch} from '../actions';

class SearchBar extends React.Component {
	render(){
		return (
				<div className="ui menu">
				  <div className="ui category search item">
				    <div className="ui transparent icon input">
				      <input className="prompt" type="text" placeholder="Search food..." onChange={e=> this.props.searchRecipe(e.target.value)}
				      onKeyDown={e=> {
				      	if(e.keyCode === 13){
				      		this.props.submitSearch(this.props.search)
				      	}
				      }}/>
				      <i onClick={e=> this.props.submitSearch(this.props.search)}className="search link icon"></i>
				    </div>
				    <div className="results">Testing</div>
				  </div>
				  {console.log(this.props.search)};
				</div>
		);
	}
}
const mapStateToProps = (state) => {
	return {search: Object.values(state.search)[0]}
}
export default connect(mapStateToProps,{searchRecipe, submitSearch})(SearchBar);

