import React from 'react';
import { connect } from 'react-redux';
import SearchBar from './SearchBar';

class App extends React.Component {
	renderList(){
		const {recipes} = this.props;
		console.log('test', recipes);
		if(!this.props.recipes){
			return (
				<div className="ui blue message">
				  <p>Enter food name to look up recipes</p>
				</div>
			);
		}
		return recipes.map(info => {
			console.log(info);
			return (
				<div className="ui items">
				  <div className="item">
				    <div className="image">
				      <img src={info.recipe.image} alt="Food"/>
				    </div>
				    <div className="content">
				      <a href={info.recipe.url}className="header">{info.recipe.label}</a>
				      <div className="meta">
				        <span>{info.recipe.ingredientLines.map(line=> {
				        	return (
				        		<p>{line}</p>
				        	);
				        })}</span>
				      </div>
				      <div className="description">
				        <p></p>
				      </div>
				      <div className="extra">
				        Additional Details
				      </div>
				    </div>
				  </div>
				</div>
			);
		})
	}
	render(){
		return (
			<div className="ui container">
				<SearchBar/>
				{this.renderList()}
			</div>
		);
	}
}
const mapStateToProps = state => {
	return {recipes: state.search.Recipes}
}
export default connect(mapStateToProps,{})(App);